import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '../pages/Home'
import Cloud from '../pages/Cloud'
import Back from '../pages/Back'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
    	path:'/Cloud',
    	name: 'Cloud',
    	component: Cloud
    },
    {
    	path:'/Back',
    	name: 'Back',
    	component: Back
    }
  ]
})



